/*global*/
body {
    font-family: 'Signika Negative', sans-serif;
}
.font-custom {
    font-size: 78px;
    font-weight: bolder;
}
/* Section */
.section {
    padding-top: 100px;
    padding-bottom: 100px;
}
/*Navbar*/
.logo {
    -o-object-fit: cover;
       object-fit: cover;
    width: 100%;
    height: 100%;
    max-width: 80px;
}
.main-nav {
    list-style: none;
    padding: 0;
}

nav {
    background-color: #1a1a1d;
    padding: 10px 0;
    text-transform: uppercase;
    font-size: 18px;
    min-height: 12vh;
    height: 100%;
    display: -webkit-box !important;
    display: -ms-flexbox !important;
    display: flex !important;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.main-nav li {
    padding: 15px 20px;
}
.main-nav li a {
    color: white;
}

.main-nav li a:hover,
.main-nav li a:focus,
.main-nav li a:active {
    text-decoration: none;
    color: #c3073f;
}
/* Footer */
.bottom-footer {
    background-color: #c3073f;
    min-height: 5vh;
    color: white;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    text-align: center;
}
footer p {
    margin: 0;
}
/*home*/
.home {
    background-color: #1a1a1d;
    min-height: 83vh;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}

.home h1,
.home h3 {
    color:  palevioletred;
}

/* About */

.section-about h2{
    color: black;
}
.pink-profile {
    -o-object-fit: cover;
       object-fit: cover;
    max-height: 250px;
    max-width: 400px;
    height: 100%;
    width: 100%;
}
.section-about .card {
    max-width: 400px;
    margin: 0 auto;
}
.travel-pic {
    -o-object-fit: cover;
       object-fit: cover;
    width: 100%;
    height: 100%;
    max-height: 180px;
}
/* Contact */
.section-contact {
    min-height: 83vh;
    height: 100%;
}
.contact-img {
    -o-object-fit: cover;
       object-fit: cover;
    width: 100%;
    height: 100%;
    max-width: 400px;

}
.contact-icon {
    position: relative;
    width: 60px;
    height: 60px;
    border-right: 1px solid black;

}
.contact-box {
    max-width: 500px;
    width: 100%;
    margin: 0px auto;
}
.contact-box h5 {
    margin: 0;
}
.contact-icon svg {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    width: 40px !important;
    height: 40px;
    color: white;
}
.contact-subbox {
    border: 1px solid black;
    border-radius: 3px;
    margin: 15px 0;
}
.phone-icon {
    background:#6f2232;
}
.email-icon {
    background:#4e4e50;
}
.github-icon {
    background:#1a1a1d;
}

.skill-list {
    width: 100%;
}
.skill-list ul{
    -webkit-column-count: 2;
       -moz-column-count: 2;
            column-count: 2;
    list-style-type: none;

}
.skill-box {
    padding: 20px 0;
    -webkit-column-break-inside: avoid;
    page-break-inside: avoid;
    -moz-column-break-inside: avoid;
         break-inside: avoid;
}

/* portfolio */
.card {
    position: relative;
    margin: 20px auto;
    max-height: 310px;
    border-radius: 10px !important;
    max-width: 345px;

}
.card img {
    border-radius: 10px;
}
.card .front,
.card .back {
    display: block;
    -webkit-transition-timing-function: cubic-bezier(.175, .885, .32, 1.275);
         -o-transition-timing-function: cubic-bezier(.175, .885, .32, 1.275);
            transition-timing-function: cubic-bezier(.175, .885, .32, 1.275);
    -webkit-transition-duration: .5s;
         -o-transition-duration: .5s;
            transition-duration: .5s;
    -webkit-transition-property: opacity, -webkit-transform;
    transition-property: opacity, -webkit-transform;
    -o-transition-property: transform, opacity;
    transition-property: transform, opacity;
    transition-property: transform, opacity, -webkit-transform;
    border-radius: 10px;
}
.card .front {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
.card .back {
    position: absolute;
    opacity: 0;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
    background-color:rgba(0,0,0,0.7);
    height: 320px;
}
.card .btn {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.card:hover .front {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
.card:hover .back {
    opacity: 1;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
.btn.btn-custom {
    color: #FFFFFF;
    text-shadow: none;
    padding: 10px 40px;
    border-radius: 0;
    border: 2px solid #ffffff;
    background-color: transparent;
    font-weight: bold;
    font-size: 16px;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}
.btn.btn-custom:hover{
    background-color: #ffffff;
    color: #c3073f;

    border-color:white;
}
span.badge {
    background: #1a1a1d;
    margin: 10px;
    padding: 5px;
    color: white;
}
.tag .badge {
    padding: 10px 20px;
}
@media (max-width: 991px) {
    img.travel-pic {
        padding: 20px;
        max-height: 100%;
    }
}
@media (max-width: 767px) {
    nav .main-nav {
        padding: 0px;
        font-size: 15px;
        float: none !important;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
    }
    .main-nav li {
        padding: 10px 3px;
    }
    .section {
        padding-top: 10%;
        padding-bottom: 10%;
    }
    .skill-list ul{
        -webkit-column-count: 1;
           -moz-column-count: 1;
                column-count: 1;
        padding: 0 20px;
    }
    .about-me {
        text-align:center;
        margin-top: 20px;
    }
    .contact-box h5 {
        font-size: 16px;
    }
    .skill-box h4 {
        font-size: 24px;
    }
    .font-custom {
        font-size: 45px;
    }
    .text-xs-center {
        text-align: center;
    }
    .bottom-footer p {
        font-size: 15px;
    }


}